<template>
	<div id="goodsinfo">
		<div class="goodsinfo_main">
			<div class="left_img">
				<img src="@/assets/test.png" alt="">
			</div>
			<div class="right_info">
				<ul>
					<li><h4>产品ID：{{gsId}}</h4></li>
					<li><p>产品名称:测试产品</p></li>
					<li><p>生产厂商:leaf公司</p></li>
					<li><p>价格:{{price}}元</p></li>
					<li><p>版权:测试产品版权</p></li>
					<li><p>地址:测试地址:112号</p></li>
					<li><a v-on:click="add_nums()" class="btn_add">+</a><input class="prod_nums" type="text" v-model="nums"><a class="btn_reduce" v-on:click="reduce_nums()">-</a></li>
					<li><p>共计:{{total}}元</p></li>
				</ul>
			</div>
		</div>
		<div class="evaluate_title_top">
			<i class="icon-evaluate_new"></i><h2>相关评价</h2>
		</div>
		<div class="evaluate_evluate">
			<ul>
				<li><span>xx用户:</span><p>我觉得很不错</p></li>
				<li><span>xx用户:</span><p>我觉得还行</p></li>
				<li><span>xx用户:</span><p>我觉得很差</p></li>
				<li><span>xx用户:</span><p>该用户未作出评价</p></li>
				<li><span>xx用户:</span><p>无信用商家，大家别上当！！</p></li>
			</ul>
		</div>
		<div class="footer_cart">
			<ul>
				<li><router-link to="/">返回首页</router-link></li>
				<li><a href="javascript:0;">购物车</a></li>
				<li><a href="javascript:0;">立即购买</a></li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'goodsinof',
		data:function(){
			return {
				gsId:0,
				price:15,
				nums:1,
			}
		},
		computed:{
			total:function(){
				return this.price*this.nums
			}
		},
		methods:{
			add_nums:function(){
				if(this.nums<100){
					this.nums++
				}
			},
			reduce_nums:function(){
				if(this.nums>0){
					this.nums--
				}
			}
		},
		watch:{
			nums:function(val){
				this.nums=val
			}
		},
		created:function() {
		    // 接收GoodsInfo.vue传过来的goodsId
		    console.log('接收'+this.$route.query.goodsId)
			this.gsId=this.$route.query.goodsId
		  },
	}
</script>

